export default "() => {\n  const [open, setOpen] = React.useState(false);\n\n  return (\n    <>\n      <button onClick={() => setOpen(true)}>Open Modal</button>\n      <YouTubeVideoModal\n        videoId=\"MLKKzRvOsLQ\"\n        translations={{\n          title: 'Video modal example',\n          poster: {\n            alt: 'Example',\n          },\n          close: {\n            ariaLabel: 'Close',\n          },\n        }}\n        isOpen={open}\n        onDismiss={() => setOpen(false)}\n        aria-label=\"Video modal example\"\n      />\n    </>\n  );\n};\n";